.form {
    margin: 1rem 0;
    overflow: auto;
  }
  
  .control {
    margin-bottom: 0.5rem;
  }
  
  .control label {
    font-weight: bold;
    margin-bottom: 0.25rem;
    display: block;
  }
  
  .control input {
    font: inherit;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 20rem;
    max-width: 100%;
  }
  
  .actions {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
  }
  
  .actions button {
    font: inherit;
    color: var(--primary-bg);
    cursor: pointer;
    background-color: transparent;
    border: none;
    border-radius: 25px;
    padding: 0.5rem 2rem; 
  }
  
  .actions button:hover,
  .actions button:active {
    background-color: #ffdcdc;
  }
  
  .actions .submit {
    border: 1px solid var(--primary-bg);
    background-color: var(--primary-bg);
    color: white;
  }
  
  .actions .submit:hover,
  .actions .submit:active {
    background-color: var(--secondary-bg);
  }
  
  .invalid label {
    color: #ca3e51;
  }
  
  .invalid input {
    border-color: #aa0b20;
    background-color: #ffeff1;
  }

  .error{
    font-size: 10px;
    margin: 0.2rem;
    padding: 0;
  }