.meal {
    display: flex;
    justify-content: space-between;
    margin: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ccc;
  }
  
  .meal h3 {
    margin: 0 0 0.25rem 0;
  }
  
  .description {
    font-style: italic;
  }
  
  .price {
    margin-top: 0.25rem;
    font-weight: bold;
    color: var(--primary-bg);
    font-size: 1.25rem;
  }

  @media (max-width:480px) {
    .meal{
      margin: 0.5rem;
      padding-bottom: 0.5rem;
    }
    .description{
      font-size: 0.75rem;
    }
    .meal h3{
      font-size: 1.2rem;
    }
    .price{
      font-size: 1rem;
    }
  }