.form {
    text-align: right;
  }
  
  .form button {
    font: inherit;
    cursor: pointer;
    background-color: var(--primary-bg);
    border: 1px solid var(--primary-bg);
    color: white;
    padding: 0.25rem 2rem;
    border-radius: 20px;
    font-weight: bold;
  }
  
  .form button:hover,
  .form button:active {
    background-color: var(--secondary-bg);
    border-color: var(--secondary-bg);
  }
  
  @media (max-width:480px) {
    .form button{
      padding: 0.20rem 1.2rem;      
    }
  }