.summary {
    text-align: center;
    max-width: 45rem;
    width: 90%;
    margin: auto;
    margin-top: -10rem;
    position: relative;
    background-color: #4b4b4b;
    color: var(--final-bg);
    border-radius: 14px;
    padding: 1rem;
    box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
  }
  
  .summary h2 {
    font-size: 2rem;
    margin-top: 0;
  }

  @media (max-width:480px) {
    .summary h2{
      font-size: 1.25rem;
    }
    .summary p{
      font-size: 0.85rem;
    }
  }