.cart-items {
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 20rem;
    overflow: auto;
  }
  
  .total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 1.5rem;
    margin: 1rem 0;
  }
  
  .actions {
    text-align: right;
  }
  
  .actions button {
    font: inherit;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid var(--primary-bg);
    padding: 0.5rem 2rem;
    border-radius: 25px;
    margin-left: 1rem;
  }
  
  .actions button:hover,
  .actions button:active {
    background-color: var(--secondary-bg);
    border-color: var(--secondary-bg);
    color: var(--final-bg);
  }
  
  .actions .button--alt {
    color: var(--primary-bg);
  }
  
  .actions .button {
    background-color: var(--primary-bg);
    color: var(--final-bg);
  }


  @media (max-width:480px) {
    .total{
      font-size: 1.2rem;
    }
    .actions button{
      padding: 0.4rem 1.2rem;
    }
  }